
import footGuide from '../../components/footer/footGuide.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

import { getShopCartList, delCart, doShopCart } from '../../service/getData'
import { getStore, setStore } from '../../config/mUtils.js'
import { formalname } from '../../config/env.js'

export default {
components: {
XHeader,
footGuide,
XButton,
Flexbox,
FlexboxItem,
XImg,
CheckIcon,
Checker,
CheckerItem,
Group,
Cell,
XNumber
},
mounted() {
var that = this.$vux.loading;

getShopCartList(formalname===1?1037:getStore('userId')).then(resolved => {

if(resolved.msgCode != '0000') {
that.show({
text: resolved.msg
})
}
setTimeout(() => {
that.hide();
}, 1000)

if(resolved.data.length == 0 || typeof resolved.data == undefined) {
this.isempty = true
}
this.list = resolved.data

})
},
methods: {
success(src, ele) {
console.log('success load', src)
const span = ele.parentNode.querySelector('span')
ele.parentNode.removeChild(span)
},
error(src, ele, msg) {
console.log('error load', msg, src)
const span = ele.parentNode.querySelector('span')
span.innerText = '加载失败'
},
change(listId, id, index, inventory, val) {
let num = 0
this.$refs.inlineNum.forEach((ele, i) => {
num += ele.currentValue * parseFloat(ele.title)
this.currentValue2 = num.toFixed(2)
if(i === index) {
this.list[index].goodsNumber = ele.currentValue

}
})
if(this.list[index].goodsNumber != val) {
var goodNum = this.list[index].goodsNumber - val

doShopCart(formalname===1?1037:getStore('userId'), id, goodNum).then(resolved => {
if(resolved.msgCode == '0000') {

} else {
this.$vux.toast.show({
text: resolved.msg
})
}
})
this.upData()
console.log(this.list[index].goodsNumber + "<<<<<>>>>>" + val)
}

},
openWindow(path){
this.$router.push({name:path})
},
pay() {
if(this.checkbox.length === 0 || this.allVal === 'NaN') {
return this.$vux.toast.show({
text: '您还没有选择商品哦',
type: 'text',
position: 'bottom',
width: '5rem'
})
}
for(var i = 0; i < this.list.length; i++) {
for(var j = 0; j < this.checkbox.length; j++) {
if(this.checkbox[j] == this.list[i].id) {
this.checkList.push(this.list[i]);
}
}
}
setStore('arrList', this.checkList)
this.$router.push({
name: 'OrderDateils',
query: {
istype: 'cart'
}
})
console.log(this.allVal)
},
onItemClick(value) {
if(value === '1') {
if(this.ischeck) {
this.ischeck = false
this.checkbox = []
this.allVal = 0
} else {
this.checkbox = []
this.ischeck = true;
this.list.forEach(function(element) {
this.checkbox.push(element.id)
}, this);
}
} else {
if(this.checkbox.length === this.list.length) {
this.checkboxAll.push('1')
this.ischeck = true
} else {
this.checkboxAll = []
this.ischeck = false
}
}
},
del(id, index) {

console.log(id)
delCart(formalname===1?1037:getStore('userId'), id).then(resolved => {
console.log(JSON.stringify(resolved))
if(resolved.msgCode == '0000') {
this.$vux.toast.show({
text: '删除成功'
})
this.list.splice(index, 1)
} else {
this.$vux.toast.show({
text: resolved.msg
})
}
})
},
upData() {
let newVal = 0
if(this.checkbox.length != 0) {
this.checkbox.forEach((element) => {
this.list.map((val, index, input) => {
if(val.id === element) {
newVal += val.goods.price * val.goodsNumber
return this.allVal = newVal.toFixed(2)
}
})
}, this)
} else {
return this.allVal = 0
}
}
},
filters: {







},
watch: {
list() {
this.list.forEach((ele) => {
return this.str += JSON.stringify(ele)
console.log(str)
})
},
checkbox() {
this.upData()
}
},
data() {
return {
title: '购物车',
isempty: false,
ischeck: false,
allVal: 0,
str: '',
checkbox: [],
checkboxAll: [],
checkList: [],
list: [{
'id': 0,
"goods": {
"attachmentCover": ""
}
}]
}
}
}



